import useContext from './useContext';

export default function useState(name, defaultValue) {
  const context = useContext();

  if (!context.__state) {
    context.__state = {};
  }

  if (!(name in context.__state)) {
    context.__state[name] =
      typeof defaultValue === 'function' ? defaultValue() : defaultValue;
  }

  function get() {
    return context.__state[name];
  }

  function set(value) {
    context.__state[name] = value;
  }

  return [get(), set, get];
}
